.header{
    background: black;
    display: flex;
    flex-direction: column;
}
.logoBar{
    position: fixed;
    background: rgb(0, 0, 0);
    height: 60px;
    width: 100vw;
    z-index: 2;
}
.logo {
    width: 60px;
    margin-top: 20px;
    margin-left: 20px;
    margin-bottom: 50px;
}

.title {
    color: white;
    font-size: 18pt;
    margin-top: 150px;
    margin-left: 20px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
}

.subTitle {
    text-align: left;
    width: 250px;
    margin-bottom: 20px;
    margin-left: 20px;
    font-family: 'Montserrat', sans-serif;
    font-size: 12pt;
    color: rgb(179, 179, 179);
    font-weight: 400;
}
/***********************************************************************************************************************************************************************************/
@media (min-width: 700px) { 
    .header{
        background: black;
        display: flex;
        height: 1000px;
        width: 100vw;
    }

    .logoBar{
        position: fixed;
        background: rgb(0, 0, 0);
        height: 60px;
        width: 100vw;
        z-index: 2;
    }

    .hero {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .logo {
        width: 80px; 
        margin-left: 100px;
        
    }

    .title {
        color: white;
        font-size: 50pt;
        margin-top: 300px;
        margin-left: 200px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 800;
    }
    
    .subTitle {
        text-align: left;
        width: 400px;
        height: 200px;
        margin-left: 200px;
        font-family: 'Montserrat', sans-serif;
        font-size: 20pt;
        color: rgb(179, 179, 179);
        font-weight: 400;
    }

}