.content {
  position: relative;
  width: 100vw;
  padding: 40px;
  background: black;
}

.input-field {
  position: relative;
  margin-bottom: 20px;
  height: 40px;
  width: 100%;
}

.input-field input[type="text"]
{
  position: absolute;
  background: transparent;
  border: none;
  box-shadow: none;
  font-size: 16px;
  color: #ffff;
}

.input-field label
{
  position: absolute;
  top: 0;
  left: 0;
  color: rgb(156, 156, 156);
  pointer-events: none;
  display: block;
  transition: 0.5s;
}

.input-field input[type="text"]:focus + label
{
  transform:  translateY(-35px);
  font-size: 12px;
  color: #ffff;
  background: #FA0C0C;
  padding: 2px 6px;
}

.input-field span 
{
  position: absolute;
  bottom: 0;
  right: 0;
  display: block;
  background: #555;
  width: 100%;
  height: 2px;
}

.input-field span::before
{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s ease-in-out;
}

.input-field input[type="text"]:focus ~ span:before
{
  transform: scaleX(1);
  transform-origin: left;
  transition: transform 0.5s ease-in-out;
}

.boton
{
  margin-top: 20px;
  border: none;
  background: #FA0C0C;
  box-shadow: none;
  padding: 10px 25px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition-delay: 0.5;
}

.boton:hover
{
  opacity: 50%;
}

